@import url(https://fonts.googleapis.com/css?family=Russo+One);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html{box-sizing:border-box;font-size:62.5%;scroll-behavior:smooth}@media (max-width: 75em){html{font-size:60%}}@media (max-width: 61.25em){html{font-size:58%}}@media (max-width: 35em){html{font-size:55%}}*{box-sizing:border-box}body{font-family:Arial, Helvetica, sans-serif;font-size:1.8rem;font-weight:400;line-height:1.5;color:#212529;text-align:left;background:#87b5b5;margin:1rem 0}.colorScheme,.editorWrap textarea,.previewWrap{background-color:#c0d8d8;border:1px solid #1d2f2f;box-shadow:1px 1px 10px 2px #3a5f5f;border-top:none}.toolbar{display:flex;align-items:center;background-color:#4aa3a3;padding:0.5rem;border:1px solid #1d2f2f;box-shadow:1px 1px 10px 2px #3a5f5f;font-family:Russo One;font-size:1.8rem;color:black}.toolbar .ico{width:25px;padding-left:0.5rem}.toolbar .ico2{margin-left:auto}.toolbar .ico2:hover{color:#57e2b1;cursor:pointer}.editorWrap{max-width:37.5em;margin:1rem auto}.editorWrap textarea{width:100%;min-height:12.5em;margin-bottom:-0.5rem;resize:vertical;font-size:1.5rem;outline:none;padding:0.5rem}.previewWrap{max-width:50em;margin:1rem auto;overflow-wrap:break-word;min-height:12.5em}.maximized{width:100%;min-height:96vh;margin:auto}.maximized textarea{min-height:92vh;margin:auto;resize:none}.hide{display:none}#preview{padding:0 1rem}#preview blockquote{border-left:3px solid #224b4b;color:#224b4b;padding-left:0.5rem;margin-left:2.5rem}#preview>p>code{padding:3px}#preview code{background:white;font-size:1.5rem;font-weight:bold}#preview pre{display:block;overflow:auto;background:white;line-height:1.2;padding:0.5 rem}#preview h1{border-bottom:2px solid #224b4b}#preview h2{border-bottom:1px solid #224b4b}#preview table{border-collapse:collapse}#preview td,#preview th{border:2px solid #224b4b;padding-left:5px;padding-right:5px}#preview img{display:block;max-width:90%;margin:2rem auto}

